<template>
    <span :type="nativeType"
            class="zui-button"
            :class="['zui-button-' + type, 'zui-button-' + size, {
              'is-disabled': disabled,
              'is-plain': plain
            }]"
            @click="handleClick"
            :disabled="disabled">
        <span class="zui-button-icon" v-if="icon || $slots.icon">
          <slot name="icon">
            <i v-if="icon" class="zui" :class="'zui-' + icon"></i>
          </slot>
        </span>
        <label class="zui-button-text"><slot></slot></label>
    </span>
</template>

<script>

    /**
     * zui-header
     * @module components/button
     * @desc 
     * @param {string} [type=default] -  default, primary, danger
     * @param {boolean} [disabled=false] - 
     * @param {boolean} [plain=false] - 
     * @param {string} [size=normal] -  normal, small, large
     * @param {string} [native-type] -  type 
     * @param {string} [icon] -  more, back .zui-xxx
     * @param {slot} - 
     * @param {slot} [icon] 
     *
     * @example
     * <zui-button size="large" icon="back" type="primary"></zui-button>
     */
    export default {
        name: 'zui-button',

        methods: {
            handleClick(evt) {
                this.$emit('click', evt);
            }
        },

        props: {
            icon: String,
            disabled: Boolean,
            nativeType: String,
            plain: Boolean,
            type: {
                type: String,
                default: 'default',
                validator(value) {
                    return [
                            'default',
                            'danger',
                            'primary',
                            'light'
                        ].indexOf(value) > -1;
                }
            },
            size: {
                type: String,
                default: 'normal',
                validator(value) {
                    return [
                            'xsmall',
                            'small',
                            'normal',
                            'large'
                        ].indexOf(value) > -1;
                }
            }
        }
    };
</script>
<style>
    .zui-button {
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
        border-radius: 4px;
        border: 0;
        box-sizing: border-box;
        color: inherit;
        display: block;
        font-size: 18px;
        width: 41px;
        line-height: 41px;
        outline: 0;
        overflow: hidden;
        position: relative;
        text-align: center
    }
    .zui-button::after {
        background-color: #000;
        content: " ";
        opacity: 0;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        position: absolute
    }
    .zui-button:not(.is-disabled):active::after {
        opacity: .4
    }
    .zui-button.is-disabled {
        opacity: .6
    }
    .zui-button-icon {
        vertical-align: middle;
        display: inline-block
    }
    .zui-button-default {
        color: #656b79;
        background-color: #f6f8fa;
        box-shadow: 0 0 1rpx #b8bbbf
    }
    .zui-button-default.is-plain {
        border: 1rpx solid #5a5a5a;
        background-color: transparent;
        box-shadow: none;
        color: #5a5a5a
    }
    .zui-button-primary {
        color: #fff;
        background-color: #26a2ff
    }
    .zui-button-primary.is-plain {
        border: 1rpx solid #26a2ff;
        background-color: transparent;
        color: #26a2ff
    }
    .zui-button-danger {
        color: #fff;
        background-color: #ef4f4f
    }
    .zui-button-danger.is-plain {
        border: 1rpx solid #ef4f4f;
        background-color: transparent;
        color: #ef4f4f
    }
    .zui-button-large {
        display: block;
        height: 100%
    }
    .zui-button-normal {
        display: inline-block;
        padding: 12px 0
    }
    .zui-button-small {
        display: inline-block;
        font-size: 14px;
        padding: 12px 0;
        width: 33px;
        line-height: 30px;
    }
</style>



